/*
 * Main Content Styles
 */

#portfolio{
  margin-top: 50px;
	padding: 100px 10%;

	img {
		border: black 5px solid;
		width: 90%;
	}

	ul > li {
		list-style: none;
		margin: none;
		padding: 0 0 20px 0;
		@media #{$brk-tablet} {
			width: 50%;
		}
		@media #{$brk-desktop} {
			width: 33.3333333%
		}
	}

	.cover-container {
		background: $shadowColor;
		width: 86.805555555556%;
		border-radius: 10px;
	}
}

.nav-portfolio {
	display: flex;
	display: -webkit-flex;
	justify-content: space-between;
	-webkit-justify-content: space-between;
	flex-wrap: wrap;
	-webkit-flex-wrap: wrap;
	margin: 0;
	padding: 0;
	@media #{$brk-tablet} {
		flex-direction: row;
		-webkit-flex-direction: row;
	}

	li {
		align-self: center;
	}
}